*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App{
  background-color: rgba(45, 79, 117, 0.767);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.container{
  background-color: blanchedalmond;
  box-shadow: 0px 0px 12px #ccc;
  border-radius: 8px;
  padding: 1rem;
}

input{
  width: 100%;
  padding: 8px 4px;
  margin: 8px 0px;
  border-radius: 8px;
}

.btn{
  display: block;
  width: 100%;
  font-size: 1.5rem;
  margin: 8px 0;
  padding: 10px 0;
  background-color: #333;
  color: #fff;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
}

.btn-outline{
  background-color: #fff;
  color: #333;
}

.center{
  text-align: center;
  margin: 20px 0;
}

.img-container{
  text-align: center;
}

.img-container img {
  height: 180px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
